export default{
	data(){
			
			
		return{
			search:"",
			list:[],
			
			TabCur: 0,
			//scrollLeft: 0
			scrolllef:[{"name":"综合"},
			{"name":"销量"},
			{"name":"价格"},
			]
		}
	},
	onLoad() {
		this.requessts();
	},
	methods:{
		xiangqing(id){
			uni.navigateTo({
				url:`../Decimal/Decimal?id=${id}`
			})
			
		},
		
	requessts:function(){
		uni.request({
			url:this.url+"Deafalut/AllContent?Shop_Information_Name="+this.search,
			method:"GET",
			//header:{'token':token},
			success: (res) => {
				console.log(res.data)
				this.list=res.data;
				// for(var item of res.data){
				// that.listimage =item.childrn
				
				// 	}
				
			}
		})
	},
	tabSelect(e) {
		this.TabCur = e.currentTarget.dataset.id;
		console.log(this.TabCur)
		if(	this.TabCur==0)
		{
		this.requessts();
		}
		else if(this.TabCur==1)
		{
	
			this.list.sort(function(a,b){
				return a.Shop_Information_Price-b.Shop_Information_Price
		})
		}
		else if(this.TabCur==2)
		{
		
			this.list.sort(function(a,b){
				return a.Shop_Information_Sell-b.Shop_Information_Sell
		})
	
		
		}
		
	},
	sousuo(){
		this.requessts();
	},
	fellei(){
		
		uni.navigateTo({
			url:"../fenlei/fenlei"
		})
	},
	getimage:function(imagepa) {
			return "http://lulumeng.qicp.vip" + imagepa;
		},
	
	
		
	}
}

